
.animated {

  position: relative;
  transition: 5s;
}

.animated .image {
  position: absolute;
  top: 0;
  animation: imageAnim 1s;
  z-index: -1;
}

.animated .text {
  position: absolute;
  top: 0;
  animation: textAnim 1s;
  z-index: 1;
}


@keyframes imageAnim {
  0% {
    transform: translateY(0);
    z-index: 1;
  }

  50% {
    transform: translateY(-60%);
    z-index: -1;
  }

  80% {
  }

  100% {
  }
}

@keyframes textAnim {
  0% {
    transform: translateY(0);
    z-index: -1;
  }

  50% {
    transform: translateY(60%);
    z-index: -1;
  }

  80% {
    z-index: 1;
  }

  100% {
  }
}